import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useMutation } from '@tanstack/react-query';
import jwtDecode from 'jwt-decode';
import { loginFn } from '../../api/user';
import { useNavigate } from 'react-router-dom';
import { Typography } from '@mui/material';
import BlackBtnLG from '../../components/UI/Atoms/Button/BlackBtnLG';
import { useSetRecoilState } from 'recoil';
import userInfoAtom from '../../recoil/atom/login/userInfoAtom';
import LoadingModal from '../../components/UI/Atoms/Spinner/LoadingModal';

const Login = () => {
  const navigate = useNavigate();
  const setUserInfo = useSetRecoilState(userInfoAtom);
  const [loading, setLoading] = useState(false);

  // 아이디 또는 비밀번호 틀렸을때 에러메세지
  const [errorsMsg, setErrorsMsg] = useState('');

  // react-hook-form
  const {
    handleSubmit,
    register,
    formState: { errors },
  } = useForm();

  // query

  const { mutate: loginMutation, isLoading } = useMutation((user) => loginFn(user), {
    onSuccess: async (data) => {
      if (data.ok === true) {
        setLoading(true);
        const userInfo = jwtDecode(data.data.accessToken);

        sessionStorage.setItem('access_token', data.data.accessToken);
        sessionStorage.setItem('refresh_token', data.data.refreshToken);
        sessionStorage.setItem('user_role', JSON.stringify(userInfo));

        // console.log(userInfo)

        setTimeout(() => {
          setUserInfo(userInfo);
          setTimeout(() => {
            if (
              userInfo?.user_role === 'USER' ||
              userInfo?.user_role === 'STUDENT' ||
              userInfo?.user_role === 'FAMILY'
            ) {
              navigate('/service/intro');
            } else {
              navigate('/home');
            }
            window.location.reload();
          }, 500);
        }, 1500);
      } else {
        setErrorsMsg(
          <>
            <div className="absolute bottom-[-2rem] left-[0.75rem] text-red-500">
              아이디 또는 비밀번호를 확인해주세요.
            </div>
          </>,
        );
        {
          setTimeout(() => {
            setErrorsMsg('');
          }, 3000);
        }
      }
    },
    onError: (error) => {
      setErrorsMsg(
        <>
          <div className="absolute bottom-[-2rem] left-[0.75rem] text-red-500">
            아이디 또는 비밀번호를 확인해주세요.
          </div>
        </>,
      );
      {
        setTimeout(() => {
          setErrorsMsg('');
        }, 3000);
      }
    },
  });

  // handler
  const onSubmit = async (user) => {
    loginMutation(user);
    // console.log(user)
  };

  // 회원가입 페이지로 이동
  const onSign = () => {
    navigate('/account/sign');
  };

  // 협회 로그인 페이지로 이동
  const onAssLogin = () => {
    navigate('/account/login/association');
  };

  // 아이디 찾기 페이지로 이동
  const onFindId = () => {
    navigate('/home/login/IDfind');
  };

  // 비밀번호 찾기 페이지로 이동
  const onFindPW = () => {
    navigate('/home/login/PWfind');
  };

  // user 정보가 있으면 home으로 이동
  useEffect(() => {
    if (sessionStorage.getItem('user_role')) {
      const userInfo = JSON.parse(sessionStorage.getItem('user_role'));
      setUserInfo(userInfo);
      navigate('/home');
    }
  }, []);

  const src = {
    logo: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/login/login_logo.png',
    bottom: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/login/login_img.png',
  };

  return (
    <>
      <div className="loginWrap py-[50px] px-[60px]">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="flex justify-center mb-[48px]">
            <img src={src.logo} />
          </div>
          <div className="flex flex-col gap-[20px] relative">
            <input
              {...register('username', { required: true })}
              className="h-[54px] rounded-[15px] px-[15px] py-[17px] text-[16px]"
              // label="아이디"
              type="text"
              name="username"
              placeholder="아이디"
            />
            <input
              {...register('password', { required: true })}
              className="h-[54px] rounded-[15px] px-[15px] py-[17px] text-[16px]"
              // label="비밀번호"
              type="password"
              name="password"
              placeholder="비밀번호"
            />
            {errorsMsg}
          </div>
          <div className="pt-[50px]">
            <BlackBtnLG
              type="submit"
              className="w-full text-[18px] text-[#000000] rounded-[15px] bg-[#FFC700] border-0 mb-[10px]"
              disabled={isLoading}
            >
              로그인
            </BlackBtnLG>
            <BlackBtnLG
              onClick={onAssLogin}
              className="w-full text-[18px] text-[#ffffff] rounded-[15px] bg-[#263EC1] border-0 mb-[12px]"
            >
              협회 로그인
            </BlackBtnLG>
          </div>
          <div className="flex justify-between">
            <div
              onClick={onSign}
              className="text-[15px] text-[#ffffff] cursor-pointer font-medium hover:text-[#AAAAAA] transition-all z-50"
            >
              회원가입
            </div>
            <div className="flex gap-[5px] z-50">
              <Typography
                onClick={onFindId}
                className="text-[15px] font-medium cursor-pointer text-[#ffffff] hover:text-[#AAAAAA] transition-all"
              >
                아이디 찾기
              </Typography>
              <span className="text-[15px] text-[#ffffff] mx-1">/</span>
              <Typography
                onClick={onFindPW}
                className="text-[15px] font-medium cursor-pointer text-[#ffffff] hover:text-[#AAAAAA] transition-all"
              >
                비밀번호 찾기
              </Typography>
            </div>
          </div>
        </form>
        {(isLoading || loading) && <LoadingModal />}
      </div>
    </>
  );
};

export default Login;
