import React from 'react';
import useWindowSize from '../../../hooks/window/useWindowSize';

const BI = () => {
  const { width } = useWindowSize();

  return (
    <div>
      <img
        src={
          width < 768
            ? 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/tkwBIM.png'
            : 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/tkwBI.png'
        }
        alt="태권월드 BI"
        className="w-full"
      />
    </div>
  );
};

export default BI;
