import React from 'react'
import useWindowSize from '../../../hooks/window/useWindowSize';

const Certification = () => {
  const { width } = useWindowSize();

  return (
    <div>
      <img
        src={
          width < 768
            ? 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/certificationM.png'
            : 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/certification.png'
        }
        alt="태권월드 디지털 승급증"
        className="w-full"
      />
    </div>
  );
}

export default Certification
