import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { useMutation } from '@tanstack/react-query';
import jwtDecode from 'jwt-decode';
import { loginFn } from '../../api/user';
import { useNavigate } from 'react-router-dom';
import { styled } from '@mui/system';
import { Typography } from '@mui/material';
import BlackBtnLG from '../../components/UI/Atoms/Button/BlackBtnLG';
import { useRecoilValue } from 'recoil';
import userInfoAtom from '../../recoil/atom/login/userInfoAtom';
import LoadingModal from '../../components/UI/Atoms/Spinner/LoadingModal';
import { getKwanjangInfo } from '../../api/Kwanjang/kwanjang';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';

const LoginSuccess = () => {
  const navigate = useNavigate();
  const userInfo = useRecoilValue(userInfoAtom);

  // handler
  const handleToHome = (e) => {
    if ('KWANJANG-SABUM'.includes(userInfo?.user_role)) {
      navigate('/home');
    }
    if ('ASSOCIATION'.includes(userInfo?.user_role)) {
      navigate('/association/home');
    }
    if ('USER-STUDENT-FAMILY'.includes(userInfo?.user_role)) {
      navigate('/service/intro');
    }
  };

  return (
    <>
      <div className="loginWrap py-[50px] pl-10">
        <img
          src={'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/login/login_logo.png'}
          className="ml-12 mb-11"
        />
        <div className="text-[#ffffff] text-[30px] font-bold mt-[5px] mb-[50px]">
          {userInfo.username && (
            <>
              {' '}
              {userInfo.username + '님,'} <br />{' '}
            </>
          )}
          안녕하세요!
        </div>
        <div className="flex gap-2">
          <BlackBtnLG
            className="w-48 text-[18px] text-[#ffffff] rounded-[15px] bg-[#000000] border-[#FFC700] mb-3"
            onClick={() => navigate('/logout')}
          >
            로그아웃
          </BlackBtnLG>
          {'KWANJANG-SABUM-ASSOCIATION'.includes(userInfo?.user_role) && (
            <BlackBtnLG
              className="w-48 text-[18px] text-[#000000] rounded-[15px] bg-[#FFC700] border-0 mb-2.5"
              onClick={handleToHome}
            >
              관리 홈으로 가기
            </BlackBtnLG>
          )}
          {'USER-STUDENT-FAMILY'.includes(userInfo?.user_role) && (
            <BlackBtnLG
              className="w-48 text-[18px] text-[#000000] rounded-[15px] bg-[#FFC700] border-0 mb-2.5"
              onClick={handleToHome}
            >
              서비스 소개
            </BlackBtnLG>
          )}
        </div>
      </div>
    </>
  );
};

export default LoginSuccess;
