import React from 'react';
import useWindowSize from '../../../hooks/window/useWindowSize';

const Service = () => {

  const {width} = useWindowSize();

  return (
    <div>
      <img
        src={
          width < 768
            ? 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/serviceM.png'
            : 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/service.png'
        }
        alt="태권월드"
        className="w-full"
      />
    </div>
  );
};

export default Service;
