import { Typography } from '@mui/material'
import React from 'react'
import useWindowSize from '../../../hooks/window/useWindowSize';

const Contact = () => {
  const { width } = useWindowSize();

  return (
    <div className={ width < 768 ?
      "w-full bg-[#f0f0f0] py-[50px]" :
      "w-full bg-[#f0f0f0] py-[90px]"
      }>
      <div
        className={
          width < 1280
            ? 'container w-[90%] m-auto mx-[20px]'
            : 'container max-md:w-full w-[1200px] m-auto'
        }
      >
        <Typography
          className={
            width < 768 ? 'mb-[30px] text-[13px] font-bold' : 'mb-[80px] text-[20px] font-bold'
          }
        >
          CONTACT
        </Typography>
        <div className="mb-[16px]">
          <div className={width < 768 ? 'flex-none' : 'flex items-center mb-[12px]'}>
            <div
              className={
                width < 768
                  ? 'text-[15px] w-[80px] text-[3e3e3e] mb-[10px]'
                  : 'text-[18px] w-[13%] text-[3e3e3e]'
              }
            >
              핸드폰 번호
            </div>
            <input
              placeholder="전화번호를 입력해주세요."
              className={
                width < 768
                  ? 'w-full h-[40px] border-0 px-[14px] mb-[10px]'
                  : 'w-[30%] h-[40px] border-0 px-[14px]'
              }
            />
          </div>
          <div className={width < 768 ? 'flex-none mb-[10px]' : 'flex items-center mb-[10px]'}>
            <div
              className={
                width < 768
                  ? 'text-[15px] w-[80px] text-[3e3e3e] mb-[10px]'
                  : 'text-[18px] w-[13%] text-[3e3e3e]'
              }
            >
              이메일
            </div>
            <div
              className={width < 768 ? 'flex items-center w-[100%]' : 'flex items-center w-[77%]'}
            >
              <input
                placeholder="이메일을 입력해주세요."
                className={
                  width < 768
                    ? 'w-[40%] h-[40px] border-0 px-[14px]'
                    : 'w-[30%] h-[40px] border-0 px-[14px]'
                }
              />
              <div className="mx-[12px]">@</div>
              <input
                placeholder="도메인을 입력해주세요."
                className={
                  width < 768
                    ? 'w-[40%] h-[40px] border-0 px-[14px] mr-[10px]'
                    : 'w-[30%] h-[40px] border-0 px-[14px] mr-[10px]'
                }
              />
              <select
                placeholder="직접 입력"
                className={
                  width < 768
                    ? 'w-[40%] h-[40px] border-0 px-[14px]'
                    : 'w-[30%] h-[40px] border-0 px-[14px]'
                }
              />
            </div>
          </div>
          <div className={width < 768 ? 'flex-none mb-[10px]' : 'flex items-center mb-[10px]'}>
            <div
              className={
                width < 768
                  ? 'text-[15px] w-[80px] text-[3e3e3e] mb-[10px]'
                  : 'text-[18px] w-[13%] text-[3e3e3e]'
              }
            >
              제목
            </div>
            <input
              placeholder="제목을 입력해주세요."
              className={
                width < 768
                  ? 'w-full h-[40px] border-0 px-[14px]'
                  : 'w-[87%] h-[40px] border-0 px-[14px]'
              }
            />
          </div>
          <div className={width < 768 ? 'flex-none mb-[10px]' : 'flex mb-[10px]'}>
            <div
              className={
                width < 768
                  ? 'text-[15px] w-[80px] text-[3e3e3e] mb-[10px]'
                  : 'text-[18px] w-[13%] text-[3e3e3e]'
              }
            >
              문의사항
            </div>
            <input
              placeholder="문의사항을 입력해주세요."
              className={
                width < 768
                  ? 'w-full h-[100px] border-0 px-[14px]'
                  : 'w-[87%] h-[188px] border-0 px-[14px]'
              }
            />
          </div>
        </div>
        <div className={width < 768 ? 'text-center' : 'text-end'}>
          <button className="w-[170px] h-[40px] bg-[#3e3e3e] text-[#ffffff] text-[16px] border-0 cursor-pointer">
            등록하기
          </button>
        </div>
      </div>
    </div>
  );
}

export default Contact
