import React from 'react';
import Carousel from 'react-material-ui-carousel';
import { Paper, Button } from '@mui/material'
import useWindowSize from '../../../hooks/window/useWindowSize';


const MainSlide = () => {
  const { width } = useWindowSize();

  return (
    <Carousel
      IndicatorIcon={false}
      interval={3000}
      animation="fade"
      NextIcon={false}
      PrevIcon={false}
      autoPlay={true}
      swipe={false}
      navButtonsAlwaysInvisible={true}
    >
      {/* 1 */}
      <Paper
        className={
          width < 768
            ? 'max-md:px-[20px] pt-96 bg-transparent shadow-none'
            : 'max-md:px-[20px] pt-40 bg-transparent shadow-none'
        }
      >
        <div
          className={
            width < 768
              ? 'text-[55px] text-[#FFC700] font-bold'
              : 'text-[80px] text-[#FFC700] font-bold'
          }
        >
          Intergration.
        </div>
        <div className="mt-5">
          <div
            className={
              width < 768
                ? 'text-[#ffffff] font-medium text-[16px] mb-2.5'
                : 'text-[#ffffff] font-medium text-[18px] mb-2.5'
            }
          >
            태권도협회, 태권도장, 수련생 및 가족들을 아우르는
          </div>
          <div
            className={
              width < 768
                ? 'text-[#ffffff] font-medium text-[16px] mb-2.5'
                : 'text-[#ffffff] font-medium text-[18px] mb-2.5'
            }
          >
            태권도인 모두에게 필요한 효율적인 태권도장
          </div>
          <div
            className={
              width < 768
                ? 'text-[#ffffff] font-medium text-[16px]'
                : 'text-[#ffffff] font-medium text-[18px]'
            }
          >
            통합 관리 서비스를 제공합니다.
          </div>
        </div>
      </Paper>
      {/* 2 */}
      <Paper
        className={
          width < 768
            ? 'max-md:px-[20px] pt-96 bg-transparent shadow-none'
            : 'max-md:px-[20px] pt-40 bg-transparent shadow-none'
        }
      >
        <div
          className={
            width < 768
              ? 'text-[70px] text-[#FFC700] font-bold'
              : 'text-[80px] text-[#FFC700] font-bold'
          }
        >
          Original.
        </div>
        <div className="mt-5">
          <div
            className={
              width < 768
                ? 'text-[#ffffff] font-medium text-[16px] mb-2.5'
                : 'text-[#ffffff] font-medium text-[18px] mb-2.5'
            }
          >
            전 세계적으로 꾸준히 성장하고 있는 유일한 무예 스포츠
          </div>
          <div
            className={
              width < 768
                ? 'text-[#ffffff] font-medium text-[16px]'
                : 'text-[#ffffff] font-medium text-[18px]'
            }
          >
            태권도는 우리나라의 대표 문화 스포츠입니다.
          </div>
        </div>
      </Paper>
      {/* 3 */}
      <Paper
        className={
          width < 768
            ? 'max-md:px-[20px] pt-96 bg-transparent shadow-none'
            : 'max-md:px-[20px] pt-40 bg-transparent shadow-none'
        }
      >
        <div
          className={
            width < 768
              ? 'text-[70px] text-[#FFC700] font-bold'
              : 'text-[80px] text-[#FFC700] font-bold'
          }
        >
          Globally.
        </div>
        <div className="mt-5">
          <div
            className={
              width < 768
                ? 'text-[#ffffff] font-medium text-[16px] mb-2.5'
                : 'text-[#ffffff] font-medium text-[18px] mb-2.5'
            }
          >
            세계가 인정하고 주목하는 스포츠
          </div>
          <div
            className={
              width < 768
                ? 'text-[#ffffff] font-medium text-[16px]'
                : 'text-[#ffffff] font-medium text-[18px]'
            }
          >
            태권월드는 태권도를 즐기는 전세계에 주목합니다.
          </div>
        </div>
      </Paper>
    </Carousel>
  );
}

export default MainSlide