// eslint-disable-next-line @typescript-eslint/no-unused-vars
import React, { useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import Login from './Login';
import LoginSuccess from './LoginSuccess';
import MainSlide from './Home/MainSlide';
import Introduce from './Home/Introduce';
import Service from './Home/Service';
import Technologe from './Home/Technologe';
import Certification from '../Common/Home/Certification';
import BI from '../Common/Home/BI';
import Contact from '../Common/Home/Contact';
import Footer from '../Common/Home/Footer';
import useWindowSize from '../../hooks/window/useWindowSize';
import { useRecoilValue } from 'recoil';
import userInfoAtom from '../../recoil/atom/login/userInfoAtom';
import SelectNation from '../Common/Home/SelectNation';

const bgImage = {
  backgroundImage: `url('https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/main/main.png')`,
  backgroundPosition: 'center',
  backgroundSize: 'cover',
  width: '100%',
  height: '100vh',
  position: 'relative',
};


const Home = () => {
  const { width } = useWindowSize();

  const sectionOne = useRef(null);
  const sectionTwo = useRef(null);
  const sectionThree = useRef(null);
  const sectionfour = useRef(null);

  const scrollToSection = (e) => {
    // console.log(sectionOne.current.offsetTop);
    if (e.current !== null) {
      const offset = e.current.offsetTop;
      window.scrollTo({
        top: offset,
        behavior: 'smooth',
      });
    }
  };


  const navigate = useNavigate();

  // recoil
  const userInfo = useRecoilValue(userInfoAtom)

  return (
    <>
      {/* TopMenu */}
      <div className="sticky top-0 z-50 bg-[#1c1c1c]">
        <div className="container max-md:w-full w-[1200px] m-auto py-4 flex items-center max-md:px-[20px]">
          <div>
            <a href="/">
              <img
                src={'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/logo/logo.png'}
                className="mr-12 max-md:w-[200px] cursor-pointer"
              />
            </a>
          </div>
          <div className="flex max-w-[620px]">
            <div
              onClick={() => scrollToSection(sectionOne)}
              className="w-[100px] font-bold text-[20px] text-[#ffffff] max-xl:hidden cursor-pointer pt-[15px]"
            >
              태권월드
            </div>
            <div
              onClick={() => scrollToSection(sectionTwo)}
              className="w-[100px] font-bold text-[20px] text-[#ffffff] max-xl:hidden cursor-pointer pt-[15px]"
            >
              서비스
            </div>
            <div
              onClick={() => scrollToSection(sectionThree)}
              className="w-[70px] font-bold text-[20px] text-[#ffffff] max-xl:hidden cursor-pointer pt-[15px]"
            >
              BI
            </div>
            <div
              onClick={() => scrollToSection(sectionfour)}
              className="w-[100px] font-bold text-[20px] text-[#ffffff] max-xl:hidden cursor-pointer pt-[15px]"
            >
              문의사항
            </div>
          </div>
          <SelectNation />
        </div>
      </div>
      <div style={bgImage}>
        <div className="container max-md:w-full w-[1200px] m-auto">
          <MainSlide />
          {width < 1200 && (
            <div className="max-md:px-[20px]">
              <div
                className={
                  width < 768
                    ? 'w-full h-12 text-[18px] text-[#000000] text-center rounded-[15px] bg-[#FFC700] border-0 translate-y-[40px] py-[10px]'
                    : 'w-full h-12 text-[18px] text-[#000000] text-center rounded-[15px] bg-[#FFC700] border-0 translate-y-96 py-[10px]'
                }
              >
                웹에서 서비스를 이용해주세요.
              </div>
            </div>
          )}
          {/* {console.log(width)} */}
        </div>

        {/* login */}
        {!userInfo && <Login />}
        {!!userInfo && <LoginSuccess />}
      </div>

      {/* content */}
      <div ref={sectionOne}>
        <Introduce />
      </div>
      <div>
        <Service />
      </div>
      <div ref={sectionTwo}>
        <Technologe />
      </div>
      <div>
        <Certification />
      </div>
      <div ref={sectionThree}>
        <BI />
      </div>
      <div ref={sectionfour}>
        <Contact />
      </div>

      {/* footer */}
      <Footer />
    </>
  );
};

export default Home;
