import React from 'react'
import useWindowSize from '../../../hooks/window/useWindowSize';

const Footer = () => {

  const { width } = useWindowSize();

  return (
    <div className="max-md:text-center bg-[#545454] text-[#999999] py-8">
      <div className="container max-md:w-full w-[1200px] m-auto">
        <div className="mb-7">
          <img
            src={'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/logo/logo.png'}
            className="max-md:w-[300px] opacity-40"
          ></img>
        </div>
        <div className={width < 768 ? 'flex-none mb-[30px]' : 'flex gap-[70px] mb-[40px]'}>
          <div
            className={width < 768 ? 'text-[14px] tracking-normal' : 'text-[15px] tracking-normal'}
          >
            <div className={width < 768 ? 'mb-[5px]' : 'mb-3'}>
              주소<span className="ml-[10px]">서울시 중랑구 동일로 151길 13, 2F</span>
            </div>
            <div className={width < 768 ? 'mb-[5px]' : 'mb-3'}>
              E-mail<span className="ml-[10px]">info@sportsent.org</span>
            </div>
            <div className={width < 768 ? 'mb-[5px]' : 'mb-3'}>
              Tel.<span className="ml-[10px]">+82. 2. 975. 1359</span>
            </div>
            <div className={width < 768 ? 'mb-[5px]' : 'mb-3'}>
              사업자등록번호<span className="ml-[10px]">830-88-02877</span>
            </div>
          </div>
          <div
            className={width < 768 ? 'text-[14px] tracking-normal' : 'text-[15px] tracking-normal'}
          >
            <div className={width < 768 ? 'mb-[5px]' : 'mb-3'}>
              어플 사용 문의
              <div className={width < 768 ? 'mt-[5px] mb-[5px]' : 'mt-[5px] mb-[20px]'}>
                jjy@sportsent.org
              </div>
            </div>
            <div className={width < 768 ? 'mb-[5px]' : 'mb-3'}>
              사업 제휴 문의
              <div className="mt-[5px]">edward@sportsent.org</div>
            </div>
          </div>
        </div>
        <address className={ width < 768 ?
          "text-[13px] tracking-normal" :
          "text-[15px] tracking-normal"
        }>
          COPYRIGHT ⓒ 2023 SPORTSENT INC. ALL RIGHTS RESERVD.
        </address>
      </div>
    </div>
  );
}

export default Footer
